header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ========= CTA Module =======*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ========= Header Socials Module =======*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 10rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ========= Picture =======*/

.Me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 22rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 50%;
    overflow: hidden;
    display: flex; /* Use flexbox to center the image */
    align-items: center;
    justify-content: center;
    padding: 0; /* Remove padding */
}

.Me img {
    width: 100%;
    height: auto;
    border-radius: 50%; /* Ensure the image itself is also circular */
}

/* ========= Scroll Down =======*/

.scroll__down {
    position: absolute;
    right: 10rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* =========== Media Queries (Medium Devices Ex: Ipad) ========== */

@media screen and (max-width: 1024px) {
    header {
        height: 60vh;
    }
}

/* =========== Media Queries (Small Devices Ex: Mobile Devices) ========== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.8rem;
        position: absolute;
        left: 0.5rem;
        bottom: 3rem;
    }

    .scroll__down {
        display: none;
    }

    .Me {
        background: linear-gradient(var(--color-primary), transparent);
        width: 20rem;
        height: 20rem;
        position: absolute;
        left: calc(50% - 10rem); /* Adjust the left position */
        margin-top: 4rem;
        border-radius: 50%;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
    }

    .Me img {
        width: 100%;
        height: auto;
        border-radius: 50%;
    }
}
